body {
  font-family: sans-serif;
}
.text-center {
  text-align: center;
  margin: 10px;
  background-color: black;
  /* text-shadow: 2px 2px; */
}
.text-center h1 {
  color: whitesmoke;
}

#app {
  text-align: center;
  height: 400px;
  width: 400px;
  margin-top: 30px;
  margin-left: 35%;
  padding-top: 5px;
  background-color: rgb(204, 203, 203);
}
.button {
  height: 50px;
  width: 50px;
  margin: 3px 0 0 1px;
  /* padding: 10px 0;  */
  text-align: center black;
  font-weight: 900;
  border-radius: 10%;
  /* box-shadow: 2px 2px; */
  cursor: pointer;
}
.optr {
  background-color: rgb(193, 61, 68);
  width: 60px;
}
.input {
  height: 35px;
  width: 210px;
  margin-bottom: 5px;
  border-radius: 4px;
  text-align: right;
  font-weight: 900;
  font-size: medium;
}
